	max-width: 100% ! important;
}

.salts .navbar-fixed-top {
    border-width: 0 0 0px ! important;
}


.salts .field.newsletter .custom-error {
	display: inline-block;
    position: absolute;
    left: 20px;
    bottom: -10px;
}

.accessoriesbox {
	display:table;
	width:100%;
	position:relative;
	@media only screen and (max-width:1024px)
			{
				display:block;
				width:100%;
			}
	.msgbox {
	position: absolute;
    bottom: -10px;
    width: 240px;
    height: 50px;
    background-color: #fff;
    border: 1px solid #eee;
    left: 0;
    right: 0;
    margin: 0 auto;
    text-transform:uppercase;
    color:#000;
    line-height:50px;
    cursor:pointer;
        text-align: center;
	}
	.firstbox {
		display:table-cell;
		vertical-align:middle;
		width:40%;
		background-image: url('http://via.placeholder.com/400x800');
		background-position:center;
		position:relative;
		@media only screen and (max-width:1024px)
			{
				display:block;
				width:100%;
				background:none;
				margin-bottom:30px;
			}
			img {
			display:none;
			@media only screen and (max-width:1024px)
			{
				display:block;
				width:100%;
				height:auto;
			}
		}
	}
	.secondbox {
		display:table-cell;
		vertical-align:middle;
		width:60%;
		padding-left:20px;		
		position:relative;
		@media only screen and (max-width:1024px)
			{
				display:block;
				padding-left:0;
				width:100%;
			}
		img {
			width:100%;
			height:auto;
		}
		.topbox {
			display:block;
			width:100%;
			margin-bottom:20px;
			position:relative;
			@media only screen and (max-width:1024px)
			{
				margin-bottom:30px;
			}
		}

		.bottombox {
		.box {
			display:inline-block;
			width: calc(50% - 10px);
			float:left;
			position:relative;

			&.leftbox {
				margin-right:10px;
				position:relative;

			@media only screen and (max-width:1024px)
			{
				margin-right:10px;
				margin-bottom:30px;
				.msgbox {
					width:100px;
				}
			}
			}
			&.rightbox {
				margin-left:10px;
				position:relative;
				@media only screen and (max-width:1024px)
			{
				margin-left:10px;
				margin-bottom:30px;
				.msgbox {
					width:100px;
				}
			}
			}
		}
	}
}
}